.header {

  &__logo {
    font-family: $font-logo;
    padding-left: 5rem;
  }

  &__accent {
    color: $color-attention;
  }

  &__nav {
    display: flex;
    font-size: 1.6rem;
  }

  &__menu-item {
    padding: 0;
  }

 &__menu-item:nth-child(1) {
   .header__link {
     color: $color-attention;

   }
 }
  &__link {
    text-decoration: none;
    font-size: 1.6rem;
    display: block;
    padding: 0 6rem;
    color: $color-background;
  }
}
