.slider {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;


  &__arrow {
  margin: 0 5px;
  cursor: pointer;
  }
  &__slide {
    display: none;
    width: 70rem;
    flex: 1;
    max-height: 44rem;
  }
  &__active {
    display: block;
  }
}







