/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type=search] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */
audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
  max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */
[hidden] {
  display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */
html {
  font-size: 10px;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  -ms-text-size-adjust: 100%;
  /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */
a:focus {
  outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */
a:active,
a:hover {
  outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */
img {
  border: 0;
  /* 1 */
  -ms-interpolation-mode: bicubic;
  /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */
figure {
  margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */
form {
  margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */
legend {
  border: 0;
  /* 1 */
  padding: 0;
  white-space: normal;
  /* 2 */
  *margin-left: -7px;
  /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */
button,
input,
select,
textarea {
  font-size: 100%;
  /* 1 */
  margin: 0;
  /* 2 */
  vertical-align: baseline;
  /* 3 */
  *vertical-align: middle;
  /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */
button,
input {
  line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */
button,
select {
  text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */
button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */
  *overflow: visible;
  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */
button[disabled],
html input[disabled] {
  cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */
input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
  *height: 13px;
  /* 3 */
  *width: 13px;
  /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */
input[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  /* 2 */
  box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */
textarea {
  overflow: auto;
  /* 1 */
  vertical-align: top;
  /* 2 */
}

/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
button,
input,
select,
textarea {
  color: #222;
}

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

img {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.4rem;
  color: #FFFFFA;
  background-color: #FFFFFA;
  padding: 10px;
}

.main {
  line-height: 3rem;
}

.col {
  display: flex;
  flex-direction: column;
  padding: 0 3.5rem;
}

.row3in1 {
  display: flex;
  padding: 3.5rem 0;
  text-align: center;
  justify-content: space-between;
  align-items: flex-start;
}

.h1 {
  font-size: 4.8rem;
}

.h2 {
  font-size: 3.6rem;
}

.h3 {
  font-size: 3.2rem;
}

.btn {
  border: none;
  background-color: #FF6600;
  color: #FFFFFA;
  font-weight: bold;
  font-size: 2rem;
}

.btn-lg {
  padding: 2rem 5rem;
}

.btn-sml {
  padding: 0;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  box-sizing: border-box;
}

.flex-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5rem 2rem;
}

.row-light {
  background-color: #5B605F;
}

.row-dark {
  background-color: #4a4a49;
}

.user {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 9rem;
  margin-bottom: 9rem;
}
.user__image {
  width: 35rem;
  height: 35rem;
  object-fit: cover;
  margin-right: 14rem;
  border: 3px solid #A1A194;
  box-shadow: 0 0 30px black;
}
.user__col {
  align-self: flex-start;
  color: black;
}
.user__text {
  font-size: 2.4rem;
  margin-top: 3rem;
  max-width: 80%;
}

.newsletter__header {
  font-size: 4.8rem;
  color: #FFFFFA;
}
.newsletter__form {
  display: flex;
}
.newsletter__text-input {
  width: 26.5rem;
  height: 100%;
  padding: 0 1rem;
  border: none;
}

.footer__col {
  text-align: left;
  max-width: 21%;
}
.footer__text {
  font-size: 2.4rem;
}
.footer__header {
  margin-bottom: 2.5rem;
}
.footer__list {
  list-style: inside;
}
.footer__text-input {
  width: 15.5rem;
  min-height: 3.5rem;
  padding: 0 1rem;
  border: none;
}
.footer__btn {
  padding: 0.6rem 1rem;
}
.footer__icons {
  font-size: 4rem;
  margin-top: 30px;
  text-decoration: none;
  color: #FFFFFA;
}

.page-bottom {
  font-size: 2rem;
  text-align: center;
}
.page-bottom__accent {
  color: #FF6600;
}
.page-bottom__container {
  padding: 2rem 0;
}

.header__logo {
  font-family: "Charmonman", cursive;
  padding-left: 5rem;
}
.header__accent {
  color: #FF6600;
}
.header__nav {
  display: flex;
  font-size: 1.6rem;
}
.header__menu-item {
  padding: 0;
}
.header__menu-item:nth-child(1) .header__link {
  color: #FF6600;
}
.header__link {
  text-decoration: none;
  font-size: 1.6rem;
  display: block;
  padding: 0 6rem;
  color: #FFFFFA;
}

.cta__header3 {
  font-size: 3.6rem;
  margin: 3rem 0;
}
.cta__content {
  padding-right: 5rem;
  max-width: 90rem;
}
.cta__par {
  font-size: 2.4rem;
  margin: 3rem 0;
}
.cta__bckg {
  background-color: #4a4a49;
}

.features {
  background-color: #FFFFFA;
  color: #5B605F;
  padding: 12rem 0;
}
.features__container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.features__txt {
  font-size: 2.4rem;
}
.features__h3 {
  padding-bottom: 2rem;
}
.features-item {
  padding-bottom: 3rem;
}
.features__col {
  width: 35rem;
  text-align: center;
  padding: 0 4rem;
}
.features ::before {
  color: #A1A194;
  font-family: "Font Awesome 5 Free";
  font-size: 10rem;
}

.slider {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}
.slider__arrow {
  margin: 0 5px;
  cursor: pointer;
}
.slider__slide {
  display: none;
  width: 70rem;
  flex: 1;
  max-height: 44rem;
}
.slider__active {
  display: block;
}

.app-header {
  padding: 2rem 0;
}
.app-header__user-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.app-header__user {
  font-size: 2.4rem;
  padding-right: 2rem;
}
.app-header__icon {
  font-size: 4rem;
  color: #FF6600;
}

.app__side-column {
  display: flex;
  flex-flow: column;
}

.app-main {
  display: flex;
  color: black;
}